
.info-box {
  background-color: white;
  border-radius: 5px;
  padding: 15px;
  border: 1px solid #ddd;
  margin-bottom: 15px;
}
.info-box header {
    border-bottom: 1px solid #ddd;
    padding: 5px 0;
}
.info-box header i {
      display: inline-block;
      position: relative;
      width: 30px;
      height: 30px;
      background-color: #3b5998;
      margin-right: 10px;
      border-radius: 50%;
      font-size: 20px;
}
.info-box header i::before {
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                    -ms-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        color: white;
}
.info-box header span {
      display: inline-block;
      position: relative;
      font-size: 20px;
      -webkit-transform: translateY(-7px);
                  -ms-transform: translateY(-7px);
              transform: translateY(-7px);
}
.info-box--body > ul.info-list {
    padding: 0;
    margin: 0;
    list-style: none;
}
.info-box--body > ul.info-list > li {
      padding: 2px 0;
}
.info-box--body > ul.info-list > li i {
        display: inline-block;
        font-size: 25px;
        color: #808080;
        width: 24px;
        text-align: center;
        margin-right: 10px;
        -webkit-transform: translateY(4px);
                    -ms-transform: translateY(4px);
                transform: translateY(4px);
}
.info-box--body > ul.skill-tags {
    padding: 0;
    margin: 0;
    list-style: none;
}
.info-box--body > ul.skill-tags > li {
      display: inline-block;
      padding: 2px 15px;
      background-color: #808080;
      margin: 10px 10px 0 0;
      border-radius: 50px;
      color: white;
}
.time-box {
  background-color: white;
  border-radius: 5px;
  padding: 15px;
  border: 1px solid #ddd;
  margin-bottom: 15px;
}
.time-box--head {
    margin-bottom: 15px;
}
.time-box--head img {
      width: 50px;
      height: 50px;
      float: left;
      margin-right: 10px;
}
.time-box--head .name {
      margin-top: 5px;
      color: #29487d;
      font-size: 20px;
}
.time-box--head .time {
      display: block;
      color: #808080;
      font-size: 12px;
}
.time-box--head .time i {
        margin-left: 10px;
}
.time-box--footer {
    padding: 5px 0 0;
    border-top: 1px solid #ddd;
}
.time-box--footer a {
      color: #666;
}
.time-box--footer i {
      margin-right: 5px;
      font-size: 18px;
}
.ig-pic {
  padding: 10px 0 0;
  margin: 0;
  list-style: none;
}
.ig-pic > li {
    float: left;
    width: 33.33%;
    padding: 0 10px 10px 0;
}
.ig-pic > li:nth-child(3n-1) {
      padding-left: 5px;
      padding-right: 5px;
}
.ig-pic > li:nth-child(3n) {
      padding-left: 10px;
      padding-right: 0;
}
.ig-pic > li img {
      position: relative;
      width: 100%;
}



