
.mainBlock {
  margin-bottom: 15px;
}
.main-visual {
  width: 100%;
  height: 0;
  padding-bottom: 37%;
  position: relative;
}
.main-visual--image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: bottom;
    font-family: "object-fit: cover; object-position: bottom;";
}
.main-visual figcaption {
    position: absolute;
    top: 0;
    right: 5px;
    text-align: right;
    color: white;
    font-size: 10px;
    opacity: .5;
}
.main-visual h2 {
    position: absolute;
    margin: 0;
    color: white;
    bottom: 10px;
    left: 180px;
    font-size: 35px;
    z-index: 2;
}
.main-visual h2 span {
      font-size: 15px;
}
.main-visual::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: linear-gradient(transparent 0%, transparent 70%, rgba(0, 0, 0, 0.5) 100%);
    z-index: 1;
}
.menu {
  position: relative;
  height: 50px;
  background-color: white;
  border: 1px solid #ddd;
  border-top: 0;
  border-radius: 0 0 5px 5px;
}
.menu--myPic {
    position: absolute;
    left: 15px;
    bottom: 15px;
    width: 150px;
    height: 0;
    padding-bottom: 150px;
    border-radius: 5px;
    border: 1px solid #808080;
    background-color: white;
    z-index: 2;
}
.menu--myPic img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      padding: 5px;
}
.menu--link {
    padding: 0;
    margin: 0;
    list-style: none;
    width: 600px;
    height: 100%;
    padding-left: 180px;
}
.menu--link > li {
      float: left;
      width: 25%;
      height: 100%;
      position: relative;
      border-left: 1px solid #ddd;
}
.menu--link > li:last-child {
        border-right: 1px solid #ddd;
}
.menu--link > li.active::before {
        content: "";
        position: absolute;
        left: 50%;
        bottom: -8px;
        border: solid #ddd;
        border-width: 1px 0 0 1px;
        width: 15px;
        height: 15px;
        background-image: url("../assets/images/subtle_grunge.png");
        -webkit-transform: translateX(-50%) rotateZ(45deg);
                    -ms-transform: translateX(-50%) rotate(45deg);
                transform: translateX(-50%) rotateZ(45deg);
}
.menu--link > li > a {
        display: block;
        height: 100%;
        width: 100%;
}
.menu--link > li > a:hover {
          background-color: rgba(0, 0, 0, 0.1);
}
.menu--link > li > a > i {
          display: none;
}
.menu--link > li > a span {
          position: absolute;
          top: 50%;
          left: 50%;
          -webkit-transform: translate(-50%, -50%);
                      -ms-transform: translate(-50%, -50%);
                  transform: translate(-50%, -50%);
          font-size: 16px;
          white-space: nowrap;
}
.menu--link > li > a span i {
            margin-left: 5px;
}
@media (max-width: 992px) {
.mainBlock {
    padding: 0;
}
.main-visual h2 {
    left: 50%;
    -webkit-transform: translateX(-50%);
                -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    white-space: nowrap;
    bottom: -115px;
    color: black;
}
.main-visual h2 span {
      display: block;
}
.menu {
    height: 200px;
    border-radius: 0;
}
.menu--myPic {
      width: 100px;
      padding-bottom: 100px;
      left: 50%;
      bottom: 150px;
      -webkit-transform: translateX(-50%);
                  -ms-transform: translateX(-50%);
              transform: translateX(-50%);
}
.menu--link {
      padding: 0;
      width: 100%;
      padding-top: 135px;
}
.menu--link > li {
        border: none;
        border-top: 1px solid #ddd;
}
.menu--link > li + li {
          border-left: 1px solid #ddd;
}
.menu--link > li:last-child {
          border-right: none;
}
.menu--link > li a {
          text-align: center;
}
.menu--link > li a > i {
            display: block;
            font-size: 24px;
}
.menu--link > li a span {
            top: 70%;
            font-size: 14px;
}
.menu--link > li a span i {
              display: none;
}
}



