
.topbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #3b5998;
  border-bottom: 1px solid #29487d;
  height: 50px;
  overflow: hidden;
  z-index: 1032;
}
.topbar--icon, .topbar--search, .topbar--right, .topbar--humbuger {
    display: inline-block;
    height: 50px;
    color: white;
}
.topbar--icon {
    position: relative;
}
.topbar--icon a {
      display: block;
      position: relative;
      width: 40px;
      height: 100%;
      color: white;
      text-decoration: none;
}
.topbar--icon a i {
        position: absolute;
        top: 50%;
        left: 0;
        -webkit-transform: translateY(-50%);
                    -ms-transform: translateY(-50%);
                transform: translateY(-50%);
        font-size: 40px;
}
.topbar--icon a span {
        padding-left: 50px;
}
.topbar--search {
    position: relative;
    width: 300px;
}
.topbar--search--wrap {
      position: absolute;
      top: 50%;
      left: 5px;
      -webkit-transform: translateY(-50%);
                  -ms-transform: translateY(-50%);
              transform: translateY(-50%);
      height: 25px;
      width: 100%;
      background-color: white;
      border-radius: 3px;
      padding: 2px 10px 0;
}
.topbar--search--wrap input {
        display: inline-block;
        border: none;
        width: 80%;
        color: black;
}
.topbar--search--wrap input:focus {
          outline: none;
          box-shadow: none;
          outline-offset: 0;
          border: none;
}
.topbar--search--wrap button {
        position: absolute;
        right: 0;
        top: 1px;
        bottom: 1px;
        width: 20%;
        background-color: #f6f7f9;
        border: none;
        border-radius: 0 2px 2px 0;
        cursor: pointer;
}
.topbar--search--wrap button:focus, .topbar--search--wrap button:active {
          outline: none;
          box-shadow: inset 1px 1px 5px rgba(0, 0, 0, 0.3);
          outline-offset: 0;
          border: none;
}
.topbar--search--wrap button i {
          font-size: 20px;
          color: black;
}
.topbar--search--wrap button i::before {
            position: absolute;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
                        -ms-transform: translate(-50%, -50%);
                    transform: translate(-50%, -50%);
}
.topbar--search a {
      display: none;
}
.topbar--right {
    float: right;
}
.topbar--right--my {
      position: relative;
      display: inline-block;
}
.topbar--right--my label {
        margin: 0;
}
.topbar--right--my figure {
        display: table;
        padding: 0 10px 0 0;
        margin: 0;
        margin-top: 10px;
        cursor: pointer;
        border-radius: 5px;
}
.topbar--right--my figure:hover {
          background-color: rgba(0, 0, 0, 0.1);
}
.topbar--right--my figure img {
          display: table-cell;
          width: 30px;
          margin-right: 10px;
          border-radius: 5px;
}
.topbar--right--my figure figcaption {
          display: table-cell;
          vertical-align: middle;
          font-size: 18px;
}
.topbar--right--my--messages {
        display: none;
}
.topbar--right--my--messages + .checkbox-popup-wrap {
          display: none;
          position: fixed;
          top: 50px;
          left: 0;
          bottom: 0;
          right: 0;
}
.topbar--right--my--messages + .checkbox-popup-wrap label {
            position: fixed;
            top: 50px;
            left: 0;
            bottom: 0;
            right: 0;
            background-color: rgba(0, 0, 0, 0.7);
            z-index: 1030;
}
.topbar--right--my--messages + .checkbox-popup-wrap .content {
            position: relative;
            color: black;
            background-color: white;
            margin-top: 5px;
            padding: 15px;
            border-radius: 5px;
            max-width: 320px;
            float: right;
            z-index: 1031;
}
.topbar--right--my--messages + .checkbox-popup-wrap .content::before {
              content: "";
              position: absolute;
              top: -18px;
              right: 120px;
              border: 10px solid transparent;
              border-color: transparent  transparent white transparent;
}
.topbar--right--my--messages:checked + .checkbox-popup-wrap {
          display: block;
}
.topbar--right--link {
      display: inline-block;
}
.topbar--right--link a {
        display: inline-block;
        position: relative;
        color: white;
        text-decoration: none;
        font-size: 17px;
        padding: 5px;
        -webkit-transform: translateY(-10px);
                    -ms-transform: translateY(-10px);
                transform: translateY(-10px);
}
.topbar--right--link a:hover {
          background-color: rgba(0, 0, 0, 0.1);
}
.topbar--right--link a::before {
          content: "";
          position: absolute;
          left: -5px;
          top: 50%;
          -webkit-transform: translateY(-50%);
                      -ms-transform: translateY(-50%);
                  transform: translateY(-50%);
          width: 1px;
          height: 80%;
          background-color: rgba(0, 0, 0, 0.1);
}
.topbar--humbuger {
    display: none;
}
@media (max-width: 992px) {
.topbar > .container {
    padding: 0;
}
.topbar--search--wrap, .topbar--right--my figure figcaption, .topbar--right--link {
    display: none;
}
.topbar--humbuger, .topbar--search a {
    display: block;
}
.topbar--right--my {
    position: relative;
    height: 100%;
    width: 100%;
}
.topbar--right--my figure img {
      margin: 0;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
                  -ms-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
}
.topbar--right--my--messages + .checkbox-popup-wrap .content::before {
      right: 35%;
}
.topbar--icon, .topbar--search, .topbar--right, .topbar--humbuger {
    float: left;
    width: 25%;
}
.topbar--icon, .topbar--search, .topbar--humbuger {
    position: relative;
}
.topbar--icon a, .topbar--search a, .topbar--humbuger a {
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
                  -ms-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      color: white;
}
.topbar--icon a i, .topbar--search a i, .topbar--humbuger a i {
        font-size: 30px;
}
}



